import React,{Component} from 'react';
import './input.css';

export default class InputArea extends Component {
    
    state = {
        firstname: '',
        lastname: '',
        email: '',
        age: '',
        country: '',
        editState: false,
        editArrayIndex: '' ,
        myArray: [],
        valuez: ''
    }

    handleFirstnameChange = (e) => {
        const firstname = e.target.value;
        this.setState({ firstname });
    }
    
    handleLastnameChange = (e) => {
        const lastname = e.target.value;
        this.setState({ lastname });
    }
    
    handleEmailChange = (e) => {
        const email = e.target.value;
        this.setState({ email });
    }
    
    handleAgeChange = (e) => {
        const age = e.target.value;
        this.setState({ age });
    }
    
    handleCountryChange = (e) => {
        const country = e.target.value;
        this.setState({ country });
    }
    
    onSubmitChange = (e) => {
        e.preventDefault();
        if(this.state.editState) {
            const editIndexArray = this.state.editArrayIndex;
            const copyArrayEdit = this.state.myArray;
            copyArrayEdit[editIndexArray] = {
                firstname : this.state.firstname ,
                lastname : this.state.lastname ,
                email : this.state.email ,
                age : this.state.age ,
                country : this.state.country
            };
            this.setState({ myArray : copyArrayEdit});
            this.setState( {editState: !this.state.editState} );
            
        }
        else {
            const firstname = this.state.firstname;
            const lastname = this.state.lastname;
            const email = this.state.email;
            const age = this.state.age;
            const country = this.state.country;
            this.setState({
                myArray: this.state.myArray.concat([{
                    firstname: firstname,
                    lastname: lastname,
                    email: email,
                    age: age,
                    country: country
                }])
            })
        }
        this.setState( {
            firstname: '',
            lastname: '',
            email: '',
            age: '',
            country: ''
        } )
    }
    
    removeItem  = (e) => {
        const deleteIndex = e.target.getAttribute('data-itemcount');
        this.state.myArray.splice(deleteIndex, 1)
        this.setState({ myArray: this.state.myArray});
        this.setState( {editState: false } );
    }
    
    editFiels = (e) => {
        const editIndex = e.target.getAttribute('data-itemcount');
        this.setState( {
            firstname: this.state.myArray[editIndex].firstname,
            lastname: this.state.myArray[editIndex].lastname,
            email: this.state.myArray[editIndex].email,
            age: this.state.myArray[editIndex].age,
            country: this.state.myArray[editIndex].country
        } )
        
        this.setState({ editArrayIndex: editIndex });
        this.setState( {editState: !this.state.editState} );
        
    }
    
    render() {
        return(
            <div className="formWrappers">
            <h1 className="headingTop">React list</h1>
               <div className="inputFormCard">
                 <form onSubmit={this.onSubmitChange}>
                    <div className="formField">
                        <label>First Name:</label>
                        <input value={this.state.firstname} onChange={this.handleFirstnameChange} type="text"  required/>
                    </div>
                    <div className="formField">
                        <label>Last Name:</label>
                        <input value={this.state.lastname} onChange={this.handleLastnameChange} type="text"  required/>
                    </div>
                    <div className="formField">
                        <label>Email:</label>
                        <input value={this.state.email} onChange={this.handleEmailChange} type="email"  required/>
                    </div>
                    <div className="formField">
                        <label>Age:</label>
                        <input value={this.state.age} onChange={this.handleAgeChange} type="text" required/>
                    </div>
                    <div className="formField">
                        <label>Country:</label>
                        <input value={this.state.country} onChange={this.handleCountryChange} type="text" required />
                    </div>
                    <button type="Submit">Submit</button>
                </form>
                </div>
                <ul className="listunstyle">
                   {this.state.myArray.map( (item, index) => (
                        <li  key={index}>
                            <span className="w100"> <strong>Name:</strong>  {item.firstname} {item.lastname} </span>
                            <span className="w100"> <strong>Email:</strong> {item.email} </span>
                            <span className="w50"> <strong>Age:</strong> {item.age} </span>
                            <span className="w50"> <strong>Country:</strong> {item.country} </span>
                            <span className="crossBtn" data-itemcount={index} onClick={this.removeItem} >close</span>
                            <span className="editBtn" data-itemcount={index} onClick={this.editFiels}> Edit </span>
                        </li>
                    ))}
                </ul>
            </div>  
        );
    }
}